//Default
Default.RequestPermission=function(success,fail){
	var old=App.__Rec;
	if(old){
		old.close();
		App.__Rec=null;
	};
	
	var rec=Recorder();
	rec.open(function(){
		//rec.close(); keep stream StopStart
		success();
	},fail);
};
Default.Start=function(set,success,fail){
	var appRec=App.__Rec;
	if(appRec!=null){
		appRec.close();//streamclose
	};
	App.__Rec=appRec=Recorder(set);
	appRec.appSet=set;
	appRec.open(function(){
		appRec.start();
		success();
	},function(msg){
		fail(msg);
	});
};
Default.Stop=function(success,fail){
	var appRec=App.__Rec;
	if(!appRec){
		if(Recorder.IsOpen()){
			//
			appRec=Recorder();
			appRec.open();
			appRec.close();
		};
		fail("");
		return;
	};
	var end=function(){
		appRec.close();
		//
		for(var k in appRec.set){
			appRec.appSet[k]=appRec.set[k];
		};
	};
	appRec.stop(function(blob,duration){
		end();
		success(blob,duration);
		App.__Rec=null;
	},function(msg){
		end();
		fail(msg);
		App.__Rec=null;
	});
};












